.skill {
    display: flex;
    box-sizing: border-box;
    width: 45%;
    margin-bottom: 30px;
    padding-right: 80px;
}

.icon {
    flex: 0 0 40px;

    img {
        width: 40px;
        max-width: 40px;
        overflow: hidden;
    }
}

.detail {
    padding-left: 20px;
}

.name {
    font-size: 20px;
    font-weight: 500;
}

.description {
    margin-top: 6px;
    line-height: 1.6em;
}
@globalColors: {
    text: #333333;
    backgroundDefault: #ffffff;
    backgroundSlight: #e8eef2;
    conversion: #36a9dc;
}

@globalWidths: {
    desktop: 1140px;
    tablet: 768px;
    mobile: 580px;
}
