body.disableScroll {
    overflow-y: hidden;
}

.wrapper {
    position: fixed;
    top: 0px;
    bottom: 0px;
    width: 100%;
    height: 100%;
    z-index: 900;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 901;
}

.gallery {
    display: flex;
    justify-content: space-between;
    position: absolute;
    @margin: 60px;
    top: @margin;
    left: @margin;
    width: calc(100% - (@margin * 2));
    height: calc(100% - (@margin * 2));
    background: #ffffff;
    border-radius: 4px;
    overflow: hidden;
    z-index: 902;
}

.close {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 20px;
    padding: 20px;
    cursor: pointer;

    &:hover path {
        fill: #333333;
    }

    path {
        fill: #aaaaaa;
    }
}

.preview {
    flex: 1;
    text-align: center;
    overflow: hidden;

    img {
        display: inline-block;
        max-width: 100%;
        max-height: 100%;
    }
}

.slides {
    height: 100%;
    transform: translateX(0%);
    transition: transform 0.2s;
    will-change: transform;
    white-space: nowrap;

    each(range(10), {
        &[data-index="@{value}"] {
            transform: translateX(@value * -100%);
        }
    });
}

.slide {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    height: 100%;
}

.thumbnails {
    padding: 20px;
    padding-top: 80px;
    background: @globalColors[backgroundSlight];
    overflow-y: auto;

    .thumbnail {
        margin-bottom: 20px;
        border: 1px solid #cccccc;
        border-radius: 4px;
        overflow: hidden;
        cursor: pointer;
        opacity: 0.7;

        &.current {
            border-color: @globalColors[conversion];
            box-shadow: 0px 0px 0px 2px @globalColors[conversion];
            opacity: 1;
        }

        &:hover {
            opacity: 1;
        }

        img {
            width: 180px;
            max-width: 180px;
            user-select: none;
            pointer-events: none;
        }
    }
}
@globalColors: {
    text: #333333;
    backgroundDefault: #ffffff;
    backgroundSlight: #e8eef2;
    conversion: #36a9dc;
}

@globalWidths: {
    desktop: 1140px;
    tablet: 768px;
    mobile: 580px;
}
